.support {
    z-index: 0;
    padding: 24px 40px;
    list-style-type: none;
    width: 335px;
    max-width: 100%;
    border-radius: 16px;
    background: radial-gradient(189.32% 190.93% at 59.76% -23.42%,
            var(--secondary-font-color) 18.03%,
            var(--quaternary-object-background-color) 100%);
    box-shadow: 0px 10px 10px 0px rgba(245, 245, 247, 0.1);

    @include large {
        width: 356px;
    }

 &__title {
        color: var(--quinary-font-color);
        font-style: normal;
        font-weight: 700;
        font-size: 22px;
        line-height: 24px;
        letter-spacing: -0.88px;
        display: flex;
        gap: 12px;
        align-items: center;
        margin-left: auto;
        margin-right: auto;

        @include medium {
            font-size: 24px;
            line-height: 28px;
            letter-spacing: -0.96px;
        }
    }

    &__icon-ua {
        fill: var(--primary-icon-color);
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 20px;
        transition: transform 0.5s ease-in-out;
      
    }

     &__item {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 16px;
        color: var(--quinary-font-color);
        height: 32px;
    }

    &__link {
        display: flex;
        
    }

    &__index {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 18px;
        letter-spacing: -0.56px;
    }

    &__icon-down {
        width: 20px;
        height: 20px;
        fill: #4F2EE8;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.swiper {
    overflow: hidden;
    height: 292px;
    width: 149px;
    margin-top: 40px;
    margin-bottom: 20px;
    margin-left: 0 !important;

    @include medium {
        margin-bottom: 24px;
    }

   @include large {
    width: 159px;
   }
}

.next,
.back {
    border: none;
    border-radius: 50%;
    width: 32px;
    height: 32px;
    display: grid;
    place-items: center;
    margin: 0 auto;

    @include medium {
        width: 38px;
        height: 38px;
        
    }
}

.btn-hidden {
    display: none;
}