  overflow: auto;
}

.side-bar__categories-item:focus {
  color: var(--secondary-border-color);
  text-transform: uppercase;
  font-weight: 700;
}

li:hover {
  cursor: pointer;
}

.books-gallery {
  background-color: peru;
  flex-direction: column;
  margin: 50px;
